.container {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: cover;
  .overlay {
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    height: 100%;
    width: 100%;
  }
}
.formContainer {
  transition: 300ms ease-in-out;
  top: 4rem;
  right: 250px;
  height: auto;
  border-radius: 20px;
  width: 500px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  z-index: 10;
  background-color: #fff;
  position: fixed;
  left: 50%;
  top: 50%;
  border: solid 5px rgba(0, 0, 0, 0.5);
  transform: translate(-50%, -50%);
  h2 {
    text-align: center;
  }
  button {
    padding: 0.5rem 1rem;
    font-family: "Playfair Display", serif;
    background: white;
    border: solid 1px black;
    transition: 300ms ease-in;
    &:hover {
      background: black;
      color: #fff;
    }
  }
  .splitInputs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  form {
    margin: 2rem 3rem;
    position: relative;

    input {
      border: solid 1px black;
      width: 100%;
      height: 2rem;
      padding-left: 0.25rem;
    }
  }
  .formGroup {
    display: block;
    margin: 0.5rem;
    label {
      display: block;
      padding: 0.5rem 0rem;
    }
    .row {
      display: grid;
      grid-template-columns: 5fr 1fr;
      input {
        width: 100%;
      }
    }
  }
  .tagAnswers {
    height: 75px;
    margin: 1rem 0rem;
    padding: 15px;
    border: solid 1px lightgray;
  }
  .smallTag {
    font-size: 0.9rem;
    display: inline;
    padding: 0rem 0.5rem;
    transition: 200ms ease-in;
    &:hover::after {
      content: "X";
      font-size: 0.7rem;
      position: absolute;
      cursor: default;
    }
  }
  .imageSample {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0.5rem;
    i {
      margin-left: 0.5rem;
    }
    img {
      height: 80px;
      width: 80px;
      object-fit: cover;
      border-radius: 50%;
      border: solid 1px black;
    }
  }
}
